.online-portal {
  background: #f5f5f5;
  padding: 40px 20px;
  text-align: center;
}

.online-portal h2 {
  margin-bottom: 24px;
  color: #333;
  letter-spacing: 1px;
}

.portal-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
}

/* CARD */
.portal-card {
  width: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

/* ICON â€“ CLEAN (NO BACKGROUND) */
.portal-card img {
  width: 40px;
  height: 40px;
  object-fit: contain;
}

/* TEXT â€“ BROWN BACKGROUND ONLY */
.portal-text {
  background: #9e1b32;   /* brown/red */
  color: #fff;
  font-size: 13px;
  padding: 6px 10px;
  border-radius: 4px;
  width: 100%;
  text-align: center;
}

/* HOVER (OPTIONAL) */
.portal-text:hover {
  background: #7f1528;
}
