.useful-links {
  padding: 40px 20px;
  text-align: center;
}

.links-row {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.logo-box {
  width: 120px;
  height: 70px;
  border: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-box img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
